	import addressed from '../../../../components/jm-address/jm-address.vue'
	var qiniu =require('qiniu-js')
	import MxDatePicker from '../../../../components/uni/mx-datepicker/mx-datepicker.vue'
	export default{
		components:{
			addressed,
			MxDatePicker
		},
		data(){
			return{
				isShow:false,
				provinceDataList:[],
				cityDataList:[],
				areaDataList:[],
				province:'',
				city:'',
				area:'',
				shop:{
					name:'',
					address:'',
					openDate:'',
					area:'',
					summary:'',
					contractor:'',//门店联系人
					telephone:'',//联系电话
					coverID:''

				},
				category:{
					id:'',
					name:''
				},
				index:-1,
				 imgList: [],
				 coverList:[],
				
				cityLobal:'',
				 titleValue:'',
				 catalogList:[],
				  type: 'rangetime',
				  showPicker: false,
				   value: '',
				   openType:'',//日期类型
				   token:'',
				   files:[]

			}
		},
		computed:{
			getOpenDate(){
				var date=new Date();
				return this.format(date,'YMD')
			}
		},
		onLoad(option){
			this.getProvinceData();
			this.getUploadToken();
			if( option && option.subID){
				this.category={
					id:option.subID,
					name:option.subName
				}
			}else if(option && option.bigID && !option.subID){
				this.category={
					id:option.bigID,
					name:option.bigName
				}
			}

		},
		methods:{
			childClick(e) {
                    console.log(e)
                },
			hideModal(){
				this.isShow=false;
				this.cityLobal=this.province.name+'-'+this.city.name+'-'+this.area.name
			},
			areaChange(){
				let index=document.getElementById("city").value;
				this.areaDataList.forEach(item=>{
					if(item.id==index){
						this.area=item;
					}
				})
			},
			cityChange(){
				let index=document.getElementById("city").value
				uni.request({
					url:this.$store.state.url+'Districts',
					data:{
						// userId:this.$store.state.userInfo.id,
						// owner:this.$store.state.userInfo.owner,
					    owner:18,
					    userId:49,
						parent:index
					},
					success:(res)=>{
						this.areaDataList=res.data.data;
						this.area=this.areaDataList[0];
						this.cityDataList.forEach(item=>{
							if(item.id==index){
								this.city=item;
								console.log(item.name)
							}
						})
					}
				})
			},
			provinceChange(){
				let index=document.getElementById("province").value
				uni.request({
					url:this.$store.state.url+'Districts',
					data:{
						// userId:this.$store.state.userInfo.id,
						// owner:this.$store.state.userInfo.owner,
					    owner:18,
					    userId:49,
						parent:index
					},
					success:(res)=>{
						this.cityDataList=res.data.data;
						this.city=this.cityDataList[0];
						this.provinceDataList.forEach(item=>{
							if(item.id==index){
								this.province=item
							}
						})
					}
				})
			},
			uploadCover(){

				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: (res) => {
					if (this.coverList.length != 0) {
						this.coverList = this.coverList.concat(res.tempFilePaths)
					} else {
						this.coverList = res.tempFilePaths
					}
					
					
					var formData = new FormData();
					formData.append("x:type", 26);
					formData.append("x:owner", 18);
					formData.append("x:creator", 43);
					formData.append("file", res.tempFiles[0]);
					console.log(formData)
					
					uni.uploadFile({
						url:this.$store.state.uploadHostUrl+this.token,
						filePath :res.tempFilePaths[0],
						name :'oaks-1'+res.tempFilePaths[0].split('/')[3]+Math.random()*100000,
						formData:formData,
						success:(res)=> {
							console.log(res)
						},
						fail:(res)=>{
							console.log(res)
						}
					})
					
					
						var putExtra={
							params:{
								// 'x:owner': this.$store.state.userInfo.owner,
								// 'x:creator': this.$store.state.userInfo.id,
								'x:type':26,
								'x:owner': 18,
								'x:creator': 43,
								// 'x:type':26,
							}
						}
						var config={
							useCdnDomain: false
						}
						console.log(res.tempFiles[0])
						
				
// 						var imgFile = new window.File([res.tempFiles[0]], 'oaks-1'+res.tempFilePaths[0].split('/')[3]+Math.random()*100000, {type: "text/plain"});
// 						console.log(imgFile)
// 
// 
// 						var observer=qiniu.upload(imgFile, 'oaks-1'+res.tempFilePaths[0].split('/')[3]+Math.random()*100000+'.png', this.token, putExtra, config);
// 						 var subscription = observer.subscribe(
// 						   ()=>{
// 						           
// 						   },
// 						   ()=>{
// 						 
// 						   },
// 						   (res)=>{
// 							  if(res.data!=-1){
// 								  uni.showToast({
// 									title:'上传成功',
// 									icon:'none'
// 								  })
// 							  }
// 							 this.shop.coverID=res.data
// 							 console.log(res.data)
// 						 
// 						   }
// 						 );
					

						
					}
				})
			},
			//验证电话号码
			checkTelEvent(event){
				console.log(event)
				if(event){
					if((/^1[34578]\d{9}$/.test(event))){

							uni.showToast({
								title:'电话号码不存在',
								icon:'none'
							})
						}
					}
				},
			//品牌类别
			showShopCatalog(){
				uni.navigateTo({
					url:'../category-item/category-item'
				})
			},
			//获得品牌类别
			getCatalog(){
				uni.request({
					url:this.$store.state.url+'Constants',
					data:{
					 //    userId:this.$store.state.userInfo.id,
						// owner: this.$store.state.userInfo.owner,
						owner:18,
						userId:49,
						type:39,
						// type: this.$store.state.constants.repair_type,
						parent: -1,
						objects: 1
					},
					success:(res) => {
						this.catalogList=res.data.data;
					}
				})
			},
			onSelected(e) {//选择
			    this.showPicker = false;
			    if(e) {
			        this[this.type] = e.value;
					if(this.openType=='enter'){
						this.shop.enterData=e.value
					}else if(this.openType=='open'){
						this.shop.openDate=e.value
					}

			    }

			},
			 onShowDatePicker(type,sys){//显示
			 this.openType=sys;
			  this.type = type;
			  this.showPicker = true;
			  this.value = this[type];
			},
			 // 三级联动选择
			showMulLinkageThreePicker() {
			  
				this.isShow=true;
			},
			recordShop(){
				console.log(this.category.id );
				console.log(this.shop.name)
				console.log(this.shop.address)
				console.log(this.province.id)
				if(!this.category.id || !this.shop.name || !this.province.id || !this.shop.address){
					uni.showToast({
						title:'请填写完整信息',
						icon:'none'
					})
				}else{

					uni.request({
						url:this.$store.state.url+'NewProprietorShop',
						data:{
							// userId:this.$store.state.userInfo.id,
							// owner:this.$store.state.userInfo.owner,
                            owner:18,
                            userId:49,
							catalog:this.category.id,
							name:this.shop.name,
							area:this.shop.area,
							address:this.shop.address,
							duedate:this.shop.openDate?this.shop.openDate:this.getOpenDate,
							summary:this.shop.summary?this.shop.summary:'',
							province:this.province.id,
							city:this.city.id,
							district:this.area.id,
							telephone:this.shop.telephone,
							contactor:this.shop.contractor,
							files:this.files?this.files.join(','):'',
							coverurl:this.shop.coverID,
							status:3,
							type:2
						},
						success: (res) => {
							uni.showToast({
								title:'录入门店成功',
								icon:'none'
							})
							setTimeout(()=>{
								uni.navigateTo({
									url:'../shop-list/shop-list'
								})
							},500)
						}
					})
				}
			},
			//获得省市区
			getProvinceData() {
				uni.request({
					url:this.$store.state.url+'Districts',
					data:{
						// userId:this.$store.state.userInfo.id,
						// owner:this.$store.state.userInfo.owner,
					    owner:18,
					    userId:49,
						parent:0
					},
					success:(res)=>{
						this.provinceDataList=res.data.data;
						this.province=this.provinceDataList[0];
					}
				})
			},
			goBack(){
				uni.navigateBack({
					delta: 1
				});
			},
			ChooseImageEvent() {
				uni.chooseImage({
					count: 9, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: (res) => {
						console.log(res)
						if (this.imgList.length != 0) {
							this.imgList = this.imgList.concat(res.tempFilePaths)
						} else {
							this.imgList = res.tempFilePaths
						}

						var putExtra={
							params:{
								// 'x:owner': this.$store.state.userInfo.owner,
								// 'x:creator': this.$store.state.userInfo.id,
								// 'x:type':this.$store.state.constants.shop_file,
                                'x:owner':18,
                                'x:creator':49,
                                'x:type':26,
							}
						}

						var config={
							 useCdnDomain: false
						}
						for(var i=0;i<res.tempFilePaths.length;i++){
							// res.tempFilePaths[i].split('/')[3]+'.jpg'

							var observer=qiniu.upload(res.tempFilePaths[i],'oaks-1'+res.tempFilePaths[i].split('/')[3]+Math.random()*100000+'.jpg' , this.token, putExtra, config);
							var subscription = observer.subscribe(
							  ()=>{

							  },
							  ()=>{

							  },
							  (res)=>{
									this.files.push(res.data)
									console.log(res)
									if(this.files.length===this.imgList.length){
										uni.showToast({
											title:'上传成功',
											icon:'success'
										})
									}
							  }
							);
						}

					},
				})
			},
			ViewImage(e,type) {
				if(type=='cover'){
					uni.previewImage({
						urls: this.coverList,
						current:2
					});
				}else if(type=='file'){
					uni.previewImage({
						urls: this.imgList,
						current: e
					});
				}

			},
			DelImg(event,type) {
				uni.showModal({
					content: '确定删除？',
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						if (res.confirm) {
							if(type=='cover'){
								this.coverList.splice(event, 1);
								uni.request({
									url:this.$store.state.url+'RemoveFiles',
									data:{
										id:this.files[event],
										owner:18,
										userId:49,
										// userId:this.$store.state.userInfo.id,
										// owner:this.$store.state.userInfo.owner,
									},
									success: (res) => {
										this.files.splice(event,1)
										uni.showToast({
											title:'删除成功',
											icon:'none'
										})
									}
								})
							}else if(type=='file'){
								this.imgList.splice(event, 1);
								uni.request({
									url:this.$store.state.url+'RemoveFiles',
									data:{
										id:this.files[event],
										// userId:this.$store.state.userInfo.id,
										// owner:this.$store.state.userInfo.owner,
                                        owner:18,
                                        userId:49,
									},
									success: (res) => {
										this.files.splice(event,1)
										uni.showToast({
											title:'删除成功',
											icon:'none'
										})
									}
								})
							}

						}
					}
				})
			},

			//获得上传图片的token
			getUploadToken(){
				uni.request({
					url:this.$store.state.url+'UploadToken',
					success: (res) => {
						this.token=res.data.data
					}
				})
			}
		}
	}
